import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

@observer
class Team extends Component {
    render() {
        const { team } = this.props;
        return (
            <div class='row mb-3'>
                <div class='col-12 col-sm-6 col-lg-2 mb-0 d-flex'>
                    {/* {JSON.stringify(team, null, 4)} */}
                    <div class={`text-center border rounded-circle imageRounded`} style={team.image ? `background-image: url("${team.image.s3SmallLink}");` : ''}>
                        {!team.image && <i class='fas fa-users text-muted mt-3' style='font-size: 40px;' />}
                    </div>
                    <div class='flex-grow-1 pl-3'>
                        <span class='float-right'>
                            {team.birth && <span class='mr-2'>{util.age(team.birth, team.deceased)} Ã¥r</span>}
                            {team.gender === 'female' ? <i class='fas fa-venus' /> : <i class='fas fa-mars' />}
                        </span>
                        <a class='stretched-link' href={`/teams/${team.id}`}>
                            <h5>{team.name}</h5>
                        </a>
                        <small>
                            {team && team.dogs && team.dogs.length} <i class='fas fa-dog mr-4' />
                            {team && team.members && team.members.length} <i class='fas fa-user' />
                        </small>
                    </div>
                </div>
            </div>
        );
    }
}

export default Team;
